* {
  margin: 0px;
  padding: 0px;
  user-select:none;
}

html,
body{
  background-color: #333333;
  height: 100%;
  width: 100%;
}

body {
  display: flex;
}

#root{
  height: 100%;
  width: 100%;
}

.App {
  height: 100%;
  width: 100%;
  text-align: center; 
  display: flex;
  flex-direction: column;
} 

.App-logo {
  height: 75%;
  pointer-events: none;
  filter: brightness(0) saturate(100%) invert(72%) sepia(85%) saturate(567%) hue-rotate(36deg) brightness(101%) contrast(110%);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

header{
  width: 100%;
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
}

/* The main content */
.content {
  height: 100%;
  flex: 16;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}

footer {
  width: 98%;
  flex: 1.5;
  display: flex;
  align-items: center;
  padding: 5px;
  padding-bottom: 2px;
  justify-content: flex-end;
}

/* Socials */
footer .right {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Logos */

.github-logo {
  height: 40px;
  margin-left: 8px;
  margin-right: 8px;
}

.yt-logo {
  height: 36px;
  margin-left: 8px;
  margin-right: 8px;
}

.insta-logo {
  height: 38px;
  margin-left: 8px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* CRT effect. From: http://aleclownes.com/2017/02/01/crt-display.html */
