.crt::after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  animation: flicker 0.15s infinite;
}

.crt::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}

.crt {
  animation: textShadow 1.6s infinite;
}

/* Code block */
pre {
  aspect-ratio: 16/9;
  background: #181818;
  border: 0px solid chartreuse;
  border-radius: 25px;
  color: chartreuse;
  font-family: monospace;
  margin: 1.6em;
  max-width: 50%;
  min-width: 200px;
  width: 75%;
  overflow: hidden;
  padding: 1.5em;
  word-wrap: break-word;
  white-space: pre-wrap;
}